//@ts-nocheck
import { keepPreviousData, useQuery } from '@tanstack/react-query';
import {
  getSwapAllTokens,
  getSwapAllTokensSearch,
  getSwapAllTokensV3,
} from '../../api';

export const useSwapAllTokens = (
  { chain }: { chain: string },
  options?: {
    onError?: (error: any) => void;
  }
) => {
  return useQuery({
    queryKey: ['getSwapAllTokens', chain],
    placeholderData: keepPreviousData,
    queryFn: async () => {
      try {
        return await getSwapAllTokensV3({ chain });
      } catch (error) {
        if (typeof options?.onError === 'function') {
          options.onError(error);
        }
        throw error;
      }
    },
  });
};

export const useSwapAllTokensSearch = (
  {
    chain,
    content,
    config,
  }: {
    chain?: string;
    content: string;
    config: { url: string; tmaid: string; tmakey: string };
  },
  options?: {
    onError?: (error: any) => void;
  }
) => {
  return useQuery({
    queryKey: ['getSwapAllTokensV2Search', chain, content, config],
    queryFn: async () => {
      try {
        return await getSwapAllTokensSearch(
          {
            content,
            chain: chain,
          },
          config
        );
      } catch (error) {
        if (typeof options?.onError === 'function') {
          options.onError(error);
        }
        throw error;
      }
    },
  });
};
