import { getTransactionsByInMessageHash } from '../config/ton';
import { Address } from '@ton/core';

const useTonTransactions = () => {
  const queryHash = async (address: string, msgHash: string) => {
    const res = await getTransactionsByInMessageHash(msgHash);
    if (res && res.length) {
      const item = res.find((item: any) => {
        const walletAddress = Address.parse(item.account).toString({
          bounceable: false,
        });
        return (
          walletAddress.toLocaleLowerCase() === address.toLocaleLowerCase()
        );
      });
      return item.hash;
    }
    return '';
  };

  return {
    queryHash,
  };
};

export default useTonTransactions;
