import { Config, getGasPrice, prepareTransactionRequest, getPublicClient } from '@wagmi/core';
import { Hex, parseUnits } from 'viem';
import { useQuery } from '@tanstack/react-query';
import { BTCNetworkAddressType, BTCNetworkType } from '../api/type';
import { btcDecimals, mockBtcEvmChainId, sendTx } from '../config/btc';
import {
  getConnection,
  getSendSplToken,
  mockSolEvmChainId,
  sendSolTx,
  ToSerializeTransaction,
} from '../config/sol';
import { IChainId, ITransactionToken, ITransactionType } from '../state/type';
import { setPassKey, sleep } from '../util';
import {
  btcSignPsbtAndPush,
  signEvmTransactionApi,
  solSignRawTransaction,
  tonSignMessage,
} from '../api';
import useTransactions from './useTransactions';
import useTomoUserInfo from './useTomoUserInfo';
import { MFAType, useMFAVerify } from './useMFAVerify';
import {
  createSigningTransaction,
  mockTonChainId,
  sendTransaction,
  TonSigningTransactionType,
} from '../config/ton';
import useChains from './useChains';
import useUserTokens from './useUserTokens';

export const useReadyBtcTransaction = (
  params: {
    network: BTCNetworkType;
    addressType: BTCNetworkAddressType | undefined;
    toAddress: string | undefined;
    amount: string;
  },
  options?: {
    onError?: (error: any) => void;
  }
) => {
  const { btcAddress } = useTomoUserInfo();

  return useQuery({
    queryKey: [
      'readyBtcTransaction',
      params.addressType,
      params.amount,
      params.network,
      params.toAddress,
      btcAddress,
    ],
    queryFn: async () => {
      try {
        return await sendTx({
          network: params.network, // my Address
          addressType: params.addressType, // toAddress
          toAddress: params.toAddress, //value
          amount: params.amount, // contract Address  // token address or undefined
        });
      } catch (error) {
        options?.onError && options?.onError(error);
        throw error;
      }
    },
  });
};

const useSendTransaction = (options?: {
  onError?: (error: any) => void;
}) => {
  const { setTransactions } = useTransactions();
  const { getMFAParams } = useMFAVerify();
  const connect = getConnection();
  const { getChain } = useChains();
  const { setCustomToken } = useUserTokens();

  const sendEVMTransaction = async (params: {
    chainId: IChainId | undefined;
    fromAddress: string | undefined;
    toAddress: string | undefined;
    value: bigint | undefined;
    rpc?: string;
    config: Config;
    data?: string | undefined;
    tokenValue?: bigint | undefined;
    token?: ITransactionToken | undefined;
    toToken?: ITransactionToken | undefined;
    mfaType?: MFAType | undefined;
    password?: string | undefined;
    historyType?: ITransactionType['historyType'];
  }) => {
    try {
      console.log('sendEVMTransaction', params);
      const data = params.data || '0x';
      const chainId = params.chainId;
      const chain = getChain(chainId);

      const gasPrice = await getGasPrice(params.config, {
        chainId: chainId,
      });

      // @ts-ignore
      const res = await prepareTransactionRequest(params.config, {
        chainId: chainId,
        account: params.fromAddress as Hex,
        to: params.toAddress as Hex,
        value: params.value || BigInt(0),
        data: data as Hex,
      });

      if (res) {
        const mfaParams = {
          gas: (res.gas * BigInt(2)).toString(),
          gasPrice: '0x' + gasPrice.toString(16),
          value: '0x' + res.value?.toString(16),
          // from: res.from,
          to: res.to,
          nonce: res.nonce,
          data: data,
          maxFeePerGas: res.maxFeePerGas?.toString(),
          maxPriorityFeePerGas: res.maxPriorityFeePerGas?.toString(),
        };
        if (!chainId) return;
        const mfaRes = await getMFAParams(
          {
            data: mfaParams,
            chainid: chainId,
          },
          params.mfaType,
          params.password
        );
        if (mfaRes) {
          const { mfa } = mfaRes;
          if (!mfa) {
            return;
          }
          setPassKey(mfa);
          const rpc = params.rpc || chain?.chain?.rpcUrls?.default?.http[0];
          if (!rpc) {
            return;
          }
          const signRes = await signEvmTransactionApi(mfa, {
            transaction: mfaParams,
            chainId,
            rpc: rpc,
          });

          const publicClient = getPublicClient(params.config)
          // @ts-ignore
          const result = await publicClient.sendRawTransaction({
            serializedTransaction: signRes.result
          })

          if (result) {
            const historySave: ITransactionType = {
              fromAddress: params?.fromAddress,
              toAddress: params?.toAddress,
              fromAmount: params?.tokenValue?.toString(),
              toAmount: params?.tokenValue?.toString() || '0',
              fromSwapTokens: params.token,
              toSwapTokens: params.toToken ? params.toToken : params.token,
              nonce: res.nonce,
              time: new Date().getTime(),
              hash: result,
              historyType: params.historyType ? params.historyType : 'Send',
              chainId: chainId,
            };

            if (params.historyType === 'Swap') {
              if (params.toToken) {
                await setCustomToken({
                  ...params.toToken,
                  token: params.toToken.address,
                  chain_id: params.toToken.chainId,
                });
              }
            }

            setTransactions({
              transaction: historySave,
            });

            return result
          }
        }
      } else {
        console.error('prepareTransactionRequest');
      }
    } catch (error) {
      console.warn({
        error,
      });
      if (error.name === 'EstimateGasExecutionError') {
        console.error(error.details);
      } else {
        console.error(error.details ? error.details : error);
      }
      options?.onError && options?.onError(error);
      throw error;
    }
  };

  const sendSolTransaction = async (params: {
    fromAddress: string | undefined;
    toAddress: string | undefined;
    value: bigint | undefined; //1e9   decimal: 9
    contract?: string;
    token?: ITransactionToken | undefined;
    toToken?: ITransactionToken | undefined;
    data?: any | undefined;
    mfaType?: MFAType | undefined;
    password?: string | undefined;
    historyType?: ITransactionType['historyType'];
  }) => {
    try {
      if (typeof params.value !== undefined && params.fromAddress) {
        let txStr;
        if (params.data) {
          txStr = await ToSerializeTransaction(params.data);
        } else {
          if (!params.contract) {
            txStr = await sendSolTx(
              params.fromAddress, // my Address
              params.toAddress, // toAddress
              params.value || BigInt(0), //value
              // signData.txMeta.mintAddress // contract Address
              params.contract
            );
          } else {
            txStr = await getSendSplToken(
              params.contract,
              params.fromAddress,
              params.toAddress,
              params.value
            );
          }
        }

        const mfaRes = await getMFAParams(
          {
            data: txStr,
            chainid: mockSolEvmChainId,
          },
          params.mfaType,
          params.password
        );

        if (mfaRes.mfa) {
          setPassKey(mfaRes.mfa);

          const signRes = await solSignRawTransaction({
            rawTransaction: txStr ?? '',
          });

          const sendTransaction = async () => {
            let hash: string;
            try {
              for (let i = 0; i < 30; i++) {
                if (signRes) {
                  hash = await connect.sendRawTransaction(
                    Buffer.from(signRes.result, 'hex')
                  );
                  await sleep(1_000);
                  const result = await connect.getSignatureStatus(hash);
                }
              }
            } catch (error) {
              if (!hash) {
                return;
              }

              const historySave: ITransactionType = {
                fromAddress: params?.fromAddress,
                toAddress: params?.toAddress,
                fromAmount: params?.value?.toString(),
                toAmount: params?.value?.toString() || '0',
                fromSwapTokens: params.token,
                toSwapTokens: params.token,
                nonce: new Date().getTime(),
                time: new Date().getTime(),
                hash: hash,
                historyType: params.historyType ? params.historyType : 'Send',
                chainId: mockSolEvmChainId,
              };

              if (params.historyType === 'Swap') {
                if (params.toToken) {
                  await setCustomToken({
                    ...params.toToken,
                    token: params.toToken.address,
                    chain_id: params.toToken.chainId,
                  });
                }
              }

              setTransactions({
                transaction: historySave,
              });

              if (hash) {
                throw { success: true, error, hash };
              }
            }
          };

          const hash = await sendTransaction();
          console.log({
            hash,
          });
          return hash;
        }
      }
    } catch (error) {
      if (error.success) {
        console.log({
          key: 'success!',
          hash: error.hash,
        });
        return error.hash;
      } else {
        console.error({
          error,
        });
      }
      options?.onError && options?.onError(error);
      throw error;
    }
  };

  const sendBtcTransaction = async (params: {
    network: BTCNetworkType;
    addressType: BTCNetworkAddressType;
    value: string;
    toAddress: string | undefined;
    token?: ITransactionToken | undefined;
    toToken?: ITransactionToken | undefined;
    mfaType?: MFAType | undefined;
    password?: string | undefined;
    historyType?: ITransactionType['historyType'];
  }) => {
    try{
      const amount = parseUnits(params.value, btcDecimals).toString();

      const txStr = await sendTx({
        network: params.network, // my Address
        addressType: params.addressType, // toAddress
        toAddress: params.toAddress, //value
        amount: amount,
      });

      const mfaRes = await getMFAParams(
        {
          data: txStr,
          chainid: mockSolEvmChainId,
        },
        params.mfaType,
        params.password
      );

      if (mfaRes) {
        setPassKey(mfaRes.mfa);

        const res = await btcSignPsbtAndPush({
          networkType: params.network,
          addressType: params.addressType, // toAddress
          psbtHex: txStr.result.psbtHex,
          autoFinalized: true,
        });

        if (res && res.result) {
          // if (tranRes.code !== 0) {
          //   throw new Error(tranRes.message)
          // }
          const hash = res.result;

          const historySave: ITransactionType = {
            fromAddress: params.network,
            toAddress: params.addressType,
            fromAmount: amount,
            toAmount: amount,
            fromSwapTokens: params.token,
            toSwapTokens: params.token,
            nonce: new Date().getTime(),
            time: new Date().getTime(),
            hash: hash,
            historyType: params.historyType ? params.historyType : 'Send',
            chainId: mockBtcEvmChainId,
          };

          if (params.historyType === 'Swap') {
            if (params.toToken) {
              await setCustomToken({
                ...params.toToken,
                token: params.toToken.address,
                chain_id: params.toToken.chainId,
              });
            }
          }

          setTransactions({
            transaction: historySave,
          });

          return hash;
        }
      }
    }catch(error){
      options?.onError && options?.onError(error);
      throw error;
    }
  };

  const sendTonTransaction = async (
    params: TonSigningTransactionType & {
      mfaType?: MFAType | undefined;
      password?: string | undefined;
      token?: ITransactionToken;
      historyType?: ITransactionType['historyType'];
    }
  ) => {
    try {
      const singingMessage = await createSigningTransaction(params);
      // console.log('signingMessageBoc', singingMessage);
      if (!singingMessage?.signingMessageBoc) return;

      const mfaRes = await getMFAParams(
        {
          data: singingMessage,
          chainid: mockTonChainId,
        },
        params.mfaType,
        params.password
      );

      const { mfa } = mfaRes;
      // console.log('mfa', mfa);
      if (!mfa) {
        return;
      }
      setPassKey(mfa);

      const mfaParams = {
        signingMessageBoc: singingMessage.signingMessageBoc,
        stateInitBoc: singingMessage?.stateInitBoc || '',
      };
      const { result: signedTransaction, code, message } = await tonSignMessage(
        mfa,
        mfaParams
      );
      // console.log('mfa', signedTransaction, code, message);
      if (code != 10000) return message;

      const tranRes = await sendTransaction(
        signedTransaction,
        params.fromAddress
      );
      // const tranRes = {'@type': 'ok', '@extra': '1724056253.9801953:10:0.38478557394306645', 'msgHash': ''};
      const amount = `${Number(params?.value) * 1e9}`;
      if (tranRes && tranRes['@type'] == 'ok') {
        const historySave: ITransactionType = {
          fromAddress: params.fromAddress,
          toAddress: params.toAddress,
          fromAmount: amount,
          toAmount: amount,
          fromSwapTokens: params.token,
          toSwapTokens: params.token,
          nonce: tranRes.lt || new Date().getTime(),
          time: new Date().getTime(),
          hash: tranRes?.msgHash || '',
          historyType: params.historyType ? params.historyType : 'Send',
          chainId: mockTonChainId,
        };

        setTransactions({
          transaction: historySave,
        });

        return tranRes?.msgHash || '';
      }
    } catch (error) {
      console.warn({
        error,
      });
      if (error.name === 'EstimateGasExecutionError') {
        console.error(error.details);
      } else {
        console.error(error.details ? error.details : error);
      }
      options?.onError && options?.onError(error);
      throw error;
    }
  };

  return {
    sendEVMTransaction,
    sendSolTransaction,
    sendTonTransaction,
    sendBtcTransaction,
  };
};

export default useSendTransaction;
