import { useQuery } from '@tanstack/react-query';
import useTomoUserInfo from './useTomoUserInfo';
import { getSystemTokens, v1AddAssetApi, v1AllAssetApi } from '../api';
import { CustomTokenParams, TokenType } from '../api/type';
import { useMemo } from 'react';

const useUserTokens = (
  tokenSize = 250,
  options?: {
    onError?: (error: any) => void;
  }
) => {
  const { evmAddress, solAddress } = useTomoUserInfo();

  const systemTokensQuery = useQuery({
    queryKey: ['TokenTypes', evmAddress, solAddress],
    queryFn: async () => {
      try {
        return await getSystemTokens({
          evm_address: evmAddress,
          solana_address: solAddress,
        });
      } catch (error) {
        if (typeof options?.onError === 'function') {
          options.onError(error);
        }
        throw error;
      }
    },
  });

  const userCustomTokensQuery = useQuery({
    queryKey: ['userCustomTokens', evmAddress, solAddress],
    queryFn: async () => {
      try {
        return await v1AllAssetApi({ page: 1, pageSize: tokenSize });
      } catch (error) {
        if (typeof options?.onError === 'function') {
          options.onError(error);
        }
        throw error;
      }
    },
  });

  const setCustomToken = async (params: CustomTokenParams) => {
    return await v1AddAssetApi(params);
  };

  const tokens: TokenType[] = useMemo(() => {
    if (!systemTokensQuery.isLoading && !userCustomTokensQuery.isLoading) {
      const systemToken = systemTokensQuery?.data;
      const userToken = userCustomTokensQuery?.data;

      const filterTokens = (userToken || []).filter(token => {
        return !systemToken.find(
          item =>
            token.token.toLocaleUpperCase() ===
              item.contract.toLocaleUpperCase() &&
            item.chain_id === token.chain_id
        );
      });
      return [
        ...(systemToken || []),
        ...(filterTokens || []).map(token => {
          const customToken: TokenType = {
            balance: '0',
            chain_id: token.chain_id,
            contract: token.token,
            decimals: token.decimals,
            image: token.image,
            is_native: !token.token,
            mercuryo_support: '',
            name: token.name,
            price: 0,
            ramp_support: '',
            symbol: token.symbol,
          };
          return customToken;
        }),
      ];
    }

    return [];
  }, [systemTokensQuery, userCustomTokensQuery]);

  return {
    tokens,
    setCustomToken,
    refetch: () => {
      systemTokensQuery.refetch();
      userCustomTokensQuery.refetch();
    },
  };
};

export default useUserTokens;
