//@ts-nocheck
import { keepPreviousData, useQuery } from '@tanstack/react-query';
import { getConnection } from '../config/sol';
import useTomoUserInfo from './useTomoUserInfo';

export const useSolBalance = (options?: { onError?: (error: any) => void }) => {
  const { solAddress } = useTomoUserInfo();
  return useQuery({
    queryKey: ['SolBalance', solAddress],
    refetchOnWindowFocus: false,
    placeholderData: keepPreviousData,
    retry: 5,
    queryFn: async () => {
      try {
        if (solAddress) {
          const connection = getConnection();
          const PublicKey = (await import('@solana/web3.js')).PublicKey;
          const publicKey = new PublicKey(solAddress);
          const balance = await connection.getBalance(publicKey);
          if (balance) {
            return BigInt(balance);
          }
        }
      } catch (error) {
        console.warn(error);
        options?.onError && options.onError(error);
      }
      return null;
    },
  });
};
