/* eslint-disable @typescript-eslint/camelcase */
import WalletTgSdk from '../v2';
import { ConnectResult, TomoContext } from '../TomoContext';
import { useCallback, useContext, useEffect, useMemo, useState } from 'react';
import ProxyLocalStorage, {
  useProxyLocalStorage,
} from '../v2/ProxyLocalStorage';
import {
  ConnectType,
  TomoOptions,
  TomoProviderEventName,
} from '../v2/types/types';
import { EthereumProvider } from '../v2/provider/EthereumProvider/EthereumProvider';

type TomoConnectOptions = {
  tonProof?: string;
  email?: string;
  workChain?: number;
};

interface Iprops {
  chain: string;
  options: TomoOptions;
  setProviders: (providers: Record<string, any>) => void;
  setConnectResult?: (result: ConnectResult) => void;
}

const useTomoConnect = ({ chain, options, setConnectResult }: Iprops) => {
  const [connected, setConnected] = useState(false);
  const { setProviders, providers, useEvmChains } = useContext(TomoContext);
  const storage = useProxyLocalStorage();
  const defaultChain = +useEvmChains[0] || 1;
  // const [evmAccounts, setEvmAccounts] = useState([]); // not in use right now

  const connect = async (connectOptions?: TomoConnectOptions) => {
    // still initialize all
    const providers = new WalletTgSdk({ injected: true, ...options });
    const { email, tonProof, workChain } = connectOptions || {};
    let res;
    try {
      if (chain === 'EVM') {
        res = await providers.ethereum.request({
          method: 'eth_requestAccounts',
          params: email ? [{ email }] : [],
        });
        if (res?.[0]) {
          setProviders(pre => ({ ...pre, ethereum: providers.ethereum }));
          providers.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [
              {
                chainId: '0x' + defaultChain.toString(16),
              },
            ],
          });
        }
      } else if (chain === 'TON') {
        const connectParams: TomoConnectOptions = {};
        if (tonProof) connectParams.tonProof = tonProof;
        if (email) connectParams.email = email;
        if (workChain) connectParams.workChain = connectParams.workChain ?? 0;
        res = await providers.tomo_ton?.connect(connectParams);
        window.tomo_ton.connected =
          window.tomo_ton?.connected || window.tomo_ton?.isConnected;
        res.address &&
          setProviders(pre => ({ ...pre, tomo_ton: providers.tomo_ton }));
        res.tonProof && setConnectResult({ result: res.tonProof });
      } else if (chain === 'SOL') {
        res = await providers.solana.connectWallet(
          email ? { email } : undefined
        );
        res.address &&
          setProviders(pre => ({ ...pre, tomo_sol: providers.solana }));
      } else if (chain === 'SUI') {
        res = await window.tomo_sui.connectWallet(
          email ? { email } : undefined
        );
      }
      // tomo connect success ?
      if ((chain === 'EVM' && res[0]) || res.address) {
        setConnected(true);
        storage.set('connect_type', 'TOMO');
      }
      return res;
    } catch (err) {
      console.log('connect error', err);
    }
  };

  // TODO  add to other providers
  const disconnectEffect = () => {
    storage.set('connect_type', '');
  };

  /* reconnection check */
  useEffect(() => {
    const storage = new ProxyLocalStorage();
    const connectType = storage.get('connect_type');
    if (connectType === 'TOMO') {
      // reconnect
      if (chain === 'EVM' && !providers.ethereum) {
        console.log('injecting reconnecting tomo evm');
        const evmProvider = new EthereumProvider({
          injected: true,
          ...options,
        });

        setProviders(pre => ({ ...pre, ethereum: evmProvider }));
      }
      if (chain === 'TON' && !providers.tomo_ton) {
        // todo
        const providers = new WalletTgSdk({ injected: true, ...options });
        setProviders(pre => ({ ...pre, tomo_ton: providers.tomo_ton }));
      }
      // sol reconnection
      if (chain === 'SOL' && !providers.tomo_sol) {
        const providers = new WalletTgSdk({ injected: true, ...options });
        setProviders(pre => ({ ...pre, tomo_sol: providers.solana }));
      }
      // sui reconnection
      if (chain === 'SUI' && !providers.tomo_sui) {
        const providers = new WalletTgSdk({ injected: true, ...options });
        setProviders(pre => ({ ...pre, tomo_sui: providers.tomo_sui }));
      }
    }
  }, [chain, providers]);

  // ethereum side effect
  useEffect(() => {
    const storage = new ProxyLocalStorage();
    const connectType = storage.get('connect_type') as ConnectType;
    if (connectType === 'TOMO') {
      providers.ethereum?.on(
        TomoProviderEventName.TOMO_EVM_PROVIDER_DISCONNECTED,
        disconnectEffect
      );
      return () => {
        providers.ethereum?.removeAllListeners?.();
      };
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [providers.ethereum]);

  // watch providers to get evm connection
  // useEffect(() => {
  //   if (providers.ethereum) {
  //     if (providers.ethereum.isConnected) {
  //       const accounts = providers.ethereum.request({
  //         method: 'eth_requestAccounts',
  //         params: [],
  //       });
  //       setEvmAccounts(accounts as any);
  //     }
  //   }
  // }, [providers]);

  // const getCurrentAccount = useCallback(() => {
  //   // if (chain === 'EVM') {
  //   //   return storage.get('accounts')?.ethereum;
  //   // } else
  //   if (chain === 'TON') {
  //     return storage.get('accounts')?.ton;
  //   } else if (chain === 'SOL') {
  //     return storage.get('accounts')?.sol;
  //   } else if (chain === 'SUI') {
  //     return storage.get('accounts')?.sui;
  //   }
  // }, [chain, storage]);

  // const currentAccount = useMemo(() => getCurrentAccount(), [
  //   getCurrentAccount,
  // ]);

  return { connect, connected };
};

export default useTomoConnect;
