import { UndefinedInitialDataOptions, useQuery } from '@tanstack/react-query';
import { buildSwapTxApi } from '../../api';

export default function useBuildSwapTx(
  params: {
    fromChainid: number;
    toChainid: number;
    fromAddress: string;
    toAddress: string;
    amount: string;
    slippage: number;
    fromWalletAddress: string;
    toWalletAddress: string;
  },
  options?: {
    refetchInterval?: boolean;
    onError?: (error: any) => void;
  }
) {
  return useQuery({
    queryKey: ['getSwapAllTokensV2Search', JSON.stringify(params)],
    queryFn: async () => {
      console.log('params', params);
      if (!params.amount || params.amount == '0') return null;
      try {
        return await buildSwapTxApi(params);
      } catch (error) {
        if (typeof options?.onError === 'function') {
          options.onError(error);
        }
        throw error;
      }
    },
    refetchInterval: options.refetchInterval ? 2000 : false,
  });
}
